#left {
	height: 100%;
	width: 25%;
	position: fixed;
	left: 0;
	background-color: black;
	opacity: 0;
}

#right {
	height: 100%;
	width: 75%;
	position: fixed;
	right: 0;
	overflow-y: scroll;
	background-color: rgb(246,244,242);
	opacity: 0;
	display: grid;
	grid-template-columns: 300px auto;
	grid-template-rows: 100px 200px auto;
}

.left-slow {
	transition: opacity 2s ease-in-out;
}

.right-slow {
	transition: opacity 2s ease-in-out;
}

.left-fast {
	transition: opacity 500ms ease-in;
}

.right-fast {
	transition: opacity 500ms ease-in;
}

#left.page-active {
	opacity: 0.5;
}

#right.page-active {
	opacity: 1;
}

.bg-img {
	z-index: -10;
	width: 100%;
	height: 100vh;
	transform: translateY(-270px);
	position: fixed;
	transition: transform 3s ease-out;
}

.bg-img.top {
	transform: translateY(0px);
}

#bg-img.bottom {
	transition: transform 3s ease-out;
	transform: translateY(-270px);
}

.bg-img-no-transition {
	z-index: -10;
	width: 100%;
	height: 100vh;
	transform: translateY(0px);
	position: fixed;
}

.content {
	width: 100vw;
	position: relative;
}

.search-bar {
	position: relative;
	margin-top: 30px;
	grid-row: 1 / 2;
	grid-column: 2 / 3;
	margin-left: 20px;
	margin-right: 40px;
}

.close-button {
	position: absolute;
	right: 40px;
	top: 15px;
	font-size: 35px;
}

.search-input {
	width: 100%;
	height: 40px;
	padding-left: 40px;
	border-radius: 50px;
	outline: none;
	border: 1px #4527A0 solid;
}

.search-button {
	height: 40px;
	width: 40px;
	border-radius: 50px;
	background-color: rgba(0,0,0,0);
	border: none;
	color: #4527A0;
	position: absolute;
	left: 3px;
	outline: none;
}

.search-button2 {
	height: 40px;
	width: 90px;
	border-bottom-right-radius: 50px;
	border-top-right-radius: 50px;
	background-color: rgba(69,39,160,1);
	border: none;
	border-left: 1px #4527A0 solid;
	color: white;
	position: absolute;
	right: 0px;
	outline: none;
	padding-right: 10px;
}

.search-button2:hover {
	background-color: rgba(69,39,160,0.9);
	color: white;
}

.search-button2:focus {
	outline: none;
}

.search-button:focus {
	outline: none;
}

.filter-menu {
	position: relative;
	width: 215px;
	height: 120px;
	opacity: 1;
	background-color: rgba(0,0,0,0);
	padding-left: 20px;
	border-radius: 3px;
	justify-items: center;
	z-index: 1;
	margin-top: 20px;
	margin-left: 10px;
}

.add-filter {
	border-bottom: 1px #E0E0E0 solid;
	margin-top: 26px;
	place-self: center;
	width: 215px;
	padding-left: 27px;
	padding-bottom: 5px;
	font-size: 19px;
	color: #424242;
}

.dropdown-item {
	width: 100%;
	height: 40px;
}

.dropdown-item p {
	border-bottom: 1px #EEEEEE solid;
	box-sizing: border-box;
	padding-bottom: 11px;
}

.filter-button {
	display: inline-block;
}


.state-filter-button:hover {
	background-color: rgba(0, 0, 0, 0);
	color: black;
	border-color: black;
}

.first-col {
	grid-row: 1 / 4;
	grid-column: 1 / 2;
	margin-left: 20px;
	margin-top: 10px;
}

.filter-group {
	position: relative;
	margin-left: 30px;
}

#state-filter-wrapper {
	width: 130px;
}

#desig-filter-wrapper {
	width: 180px;
}

#search-filter-wrapper {
	width: 180px;
}

.state-filter-item {
	position: relative;
	background-color: #00BCD4;
	border-radius: 50px;
	padding-left: 15px;
	padding-right: 15px;
	padding-top: 5px;
	padding-bottom: 5px;
	margin-top: 12px;
	display: inline-block;
}

.state-filter-item:hover {
	box-shadow: 0 .5rem 1rem rgba(0,0,0,.15)!important;
}

.state-filter-item i:hover {
	font-size: 17px;
}

.desig-filter-item {
	position: relative;
	background-color: #9575CD;
	border-radius: 50px;
	padding-left: 15px;
	padding-right: 15px;
	padding-top: 5px;
	padding-bottom: 5px;
	margin-top: 12px;
	display: inline-block;
}

.desig-filter-item:hover {
	box-shadow: 0 .5rem 1rem rgba(0,0,0,.15)!important;
}

.desig-filter-item i:hover {
	font-size: 17px;
}

.desig-filter-item i {
	color: white;
	position: absolute;
	top: 50%;
	transform: translate(0, -50%);
	right: 12px;
}

.state-filter-item i {
	color: white;
	position: absolute;
	top: 50%;
	transform: translate(0, -50%);
	right: 12px;
}

.filter-text {
	height: 100%;
	margin-right: 20px;
	font-size: 13px;
	font-family: 'Muli', sans-serif;
}


.search-result {
	grid-column: 2 / 3;
	grid-row: 2 / 4;
}

.result-item {
	border-bottom: 1px #E0E0E0 solid;
	background-color: rgba(0,0,0,0);
	margin-left: 10px;
	margin-right: 30px;
	padding: 20px;
	opacity: 0.9;
}

.result-item .result-name {
	font-family: 'Merriweather', serif;
	font-size: 27px;
	margin-bottom: 0px;
}

.result-item .result-state {
	margin-right: 30px;
	color: #0D47A1;
}

.result-item .state-desig {
	font-family: 'Muli', sans-serif;
}

.result-item .result-desig {
	color: #4A148C;
}

.result-item .result-dest {
	color: #616161;
	font-family: 'Muli', sans-serif;
	font-size: 14px;
}

.result-name .result-link-icon {
	font-size: 15px;
	margin-left: 7px;
	transform: translateY(-3px);
	color: #1565C0;
}

.result-item:hover {
	color: #1565C0;
}

.result-link {
	color: black;
}

.result-link:hover {
	text-decoration: none;
	color: black;
}

.rotate-90 {
	transform: rotate(90deg);
}

.collapse-icon {
	margin-right: 11px;
	color: #3CDBBC;
}

.filter-option {
	padding-top: 5px;
	background-color: white;
	visibility: hidden;
	position: absolute;
	left: 20px;
	z-index: 10;
	border-radius: 10px;
	border: 1px #9E9E9E solid;
}

.filter-option::after {
	content: '';
	position: absolute;
	top: 0;
	left: 50%;
	width: 0;
	height: 0;
	border: 10px solid transparent;
	border-bottom-color: white;
	border-top: 0;
	margin-left: -45px;
	margin-top: -8px;
}

.filter-option::before {
	content: '';
	position: absolute;
	top: 0;
	left: 50%;
	width: 0;
	height: 0;
	border: 10px solid transparent;
	border-bottom-color: #BDBDBD;
	border-top: 0;
	margin-left: -45px;
	margin-top: -10px;
}

#state-filter-option::after, #state-filter-option::before {
	margin-left: -75px;
}

#desig-filter-option::after, #desig-filter-option::before {
	margin-left: -70px;
}

.show {
	visibility: visible;
}

/* Disable text selection highlighting */
.noselect {
  -webkit-touch-callout: none;
    -webkit-user-select: none;
     -khtml-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.option-wrapper {
	max-height: 0px;
	transition: max-height 500ms ease;
	overflow-y: scroll;
}

.option-wrapper-sm {
	max-height: 0px;
	overflow: hidden;
	transition: max-height 500ms ease;
}

.expand {
	max-height: 270px;
}

.expand-sm {
	max-height: 100px;
}

.filter-wrapper {
	cursor: pointer;
}

.filter-wrapper i {
	transition: color 200ms ease, transform 250ms ease-in-out;
}

.filter-wrapper:hover i {
	color: #00796B;
}

#search-by-name {
	border: none;
}

#dropdown-name {
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
}

.loading-page {
	grid-row: 2 / 4;
	grid-column: 2 / 3;
	margin-left: 20px;
	margin-right: 90px;
	margin-bottom: 190px;
	position: relative;
}

.sun-loading {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}

#sun-icon {
	width: 70px;
	animation: sun-spin infinite 17s linear;
}

@keyframes sun-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.sun-eye {
	width: 6px;
	height: 6px;
	background-color: white;
	border-radius: 50%;
	position: absolute;
	z-index: 1;
	top: 27px;
	box-sizing: border-box;
	animation: eye-blink 2s infinite;
}

#eye1 {
	left: 24px;

}

#eye2 {
	left: 41px;
	
}

@keyframes eye-blink {
    0%, 100% {
        transform: scale(1, .05);
    }
    15%, 85% {
        transform: scale(1, 1);
    }
}

.loading-text {
	color: #FFC107;
	margin-top: 5px;
	font-size: 16px;
}

.loading-dots {
	position: absolute;
	top: 76px;
	left: 56px;
}

.error-page {
	grid-row: 2 / 4;
	grid-column: 2 / 3;
	position: relative;
	margin-left: 20px;
}

.error-message {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}

#error-icon {
	width: 400px;
}

.error-text {
	position: absolute;
	top: 175px;
	left: 212px;
	font-weight: bold;
	font-family: 'Roboto Condensed', sans-serif;
	color: white;
	font-size: 18px;
}

.text-went-wrong {
	font-family: 'Noto Sans HK', sans-serif;
	font-size: 25px;
	position: absolute;
	left: 30px;
	top: 0px;
	color: #BF360C;
}

.error-instr {
	position: absolute;
	top: 100px;
	left: 40px;
	width: 150px;
}



.no-result-found {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}

#snow-icon {
	width: 70px;
	position: absolute;
	left: 50%;
	transform: translate(-50%, 0);
}

.no-result-page {
	grid-row: 2 / 4;
	grid-column: 2 / 3;
	margin-left: 20px;
	margin-right: 90px;
	margin-bottom: 190px;
	position: relative;
}

.no-result-text {
	color: #A1B7FF;
	margin-top: 80px;
}

.load-more-page {
	height: 170px;
	width: 100%;
	position: relative;
}

.load-more {
	position: absolute;
	left: 40%;
	top: 50%;
	transform: translate(0, -50%);
	width: 80px;
	height: 80px;
}

.load-more:hover {
	cursor: pointer;
}

.load-more:hover .load-more-icon {
	width: 53px;
}

.load-more-icon {
	width: 50px;
	margin-left: 12px;
	margin-bottom: 10px;
}

.load-more-text {
	color: #07AEEA;
	position: absolute;
	bottom: 0px;
}

.loading-more-icon {
	position: absolute;
	left: 19px;
}

.return-to-top {
	position: fixed;
	right: 35px;
	bottom: 20px;
	width: 70px;
	height: 70px;
	border-radius: 50%;
	background-color: #212121;
	cursor: pointer;
	visibility: hidden;
	z-index: 1;
	opacity: 0;
	transition: opacity 200ms linear;
}

.visible {
	opacity: 0.2;
}

.return-to-top:hover {
	opacity: 0.5;
}

.top-arrow {
	position: absolute;
	font-size: 34px;
	left: 21px;
	top: 14px;
	color: #F6F4F2;
}

.top-bar {
	position: absolute;
	width: 32px;
	height: 5px;
	top: 17px;
	border-radius: 3px;
	left: 20px;
	background-color: #F6F4F2;
}


/* Style for side nav bar */

.side-nav-container2 {
	height: 100%;
	width: 25%;
	position: fixed;
	left: 0;
	background-color: black;
	font-family: 'Open Sans', sans-serif;
	opacity: 0;
	transition: opacity 250ms ease-in;
	z-index: 0;
	color: white;
	display: grid;
	grid-template-rows: 1fr 400px 1fr;
	grid-template-columns: 1fr 200px 1fr;

}

.side-nav-show {
	opacity: 0.6;
	z-index: 1;
}

.side-nav-menu {
	grid-row: 2 / 3;
	grid-column: 2 / 3;
	place-self: center;
	font-family: 'Muli', sans-serif;
}

.side-nav-home {
	transform: translateX(17px);
}

.side-nav-search {
	transform: translateX(13px);
}

.side-nav-link {
	font-size: 20px;
	height: 30px;
	color: white;
	display: block;
	margin-bottom: 20px;
	cursor: pointer;
}

.side-nav-link:hover .line-grow::after {
	transform: scaleX(1);
}

.side-nav-link:hover {
	color: white;
}

.side-nav-item {
	display: inline-block;
}

#nav-icon {
	transition: opacity 100ms linear;
}

.opacity-zero {
	opacity: 0;
}

#nav-icon.opacity-zero:hover {
	opacity: 0;
}



@media only screen and (max-width: 1280px) {
	.bg-img {
		z-index: -10;
		width: 100%;
		height: 100vh;
		transform: translateY(-200px);
		position: fixed;
		transition: transform 3s ease-out;
	}

	#bg-img.bottom {
		transform: translateY(-200px);
	}
}

@media only screen and (max-width: 1024px) {
	.bg-img {
		z-index: -10;
		width: 100%;
		height: 100vh;
		transform: translateY(-130px);
		position: fixed;
		transition: transform 3s ease-out;
	}

	#bg-img.bottom {
		transform: translateY(-130px);
	}
}
